body {
  background: linear-gradient(45deg, #EAD196, #F3EDC8, #B0D9A9, #91C4A1, #AED9E0);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  animation: backgroundAnimation 20s linear infinite alternate;
  /* overflow:hidden; */
}

@keyframes backgroundAnimation {
  0% {
    background-position: 0% 0%;
  }

  100% {
    background-position: 100% 100%;
  }
}

.form {
  position: absolute;
  top: 50%;
  left: 75%;
  transform: translate(-50%, -50%);
  width: 300px;
  padding: 10px;
  border: solid black 2px;
  background-color: #F3EDC8;
  overflow: hidden;
}

.form .form-header {
  height: 30px;
  display: flex;
  align-items: center;
  text-align: center;
  cursor: pointer;
}

.form .signup {
  height: 25%;
  width: 25%;
  border: solid black 2px;
}

.form .form-header>div {
  color: #F3EDC8;
  font-size: 25px;
  font-weight: 900;
}

.form.signup .form-header div.show-signup {
  color: #d15931;
}

.form .form-elements {
  margin-top: 15px;
}

.form .form-elements .form-element {
  height: 50px;
  opacity: 1;
  overflow: hidden;
}

.form .form-elements input {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  margin: 5px 0px;
  border-radius: 10px;
  box-sizing: border-box;
  border: 1px solid #222;
  background: #f5f5f5;
}

.form .form-elements button {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  font-weight: 600;
  margin-top: 5px;
  border-radius: 10px;
  background-color: #EAB2A0;
  color: #f5f5f5;
  cursor: pointer;
  border: none;
  outline: none;
}

.form .form-elements button:hover {
  background-color: #BF3131;
}

#logo {
  align-items: center;
  animation: movedown 1s linear 1;
  animation-delay: 0.3s;
  visibility: hidden;
  animation-fill-mode: forwards;
  color: #EAB2A0;
  font-size: 4rem;
  font-weight: 900;
  font-family: Georgia, 'Times New Roman', Times, serif;
  text-transform: uppercase;
  letter-spacing: 4px;
  text-shadow: 0 0 10px rgba(255, 0, 0, 0.8), 0 0 20px rgba(255, 0, 0, 0.5), 0 0 30px rgba(255, 0, 0, 0.3);
  /* width:100rem;   */
}

#top :nth-child(1) {
  text-align: center;
  animation: movedown 1s linear 1;
  animation-delay: 0.3s;
  visibility: hidden;
  animation-fill-mode: forwards;
}

#top :nth-child(2) {
  text-align: center;
  animation: movedown 2s linear 1;
  animation-delay: 0.3s;
  visibility: hidden;
  animation-fill-mode: forwards;
}

#top :nth-child(3) {
  text-align: center;
  animation: movedown 3s linear 1;
  animation-delay: 0.3s;
  visibility: hidden;
  animation-fill-mode: forwards;
}

@keyframes movedown {
  0% {
    transform: translateY(-100px);
    visibility: visible;
  }

  100% {
    transform: translateY(-0px);
    visibility: visible;
  }
}

.division {
  justify-content: space-between;
  flex-direction: row;
  flex-shrink: 4;
  flex-grow: 3;
  flex-flow: wrap;
}


#right {
  color: #3C0753;
  margin-left: 10%;
  margin-top: 4%;
  margin-bottom: 15%;
  width: 35%;
  height: 30rem;
  font-size: x-large;
  background-attachment: local;
  background-size: cover;
  font-family: 'Times New Roman', Times, serif;
  font-style: italic;
  font-weight: 900;
  text-size-adjust: inherit;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.line {
  opacity: 0.3;
}
#line1{
  font-size: 5rem;
  line-height: 1;
}
#line2{
  font-size: 2.25rem;
    line-height: 2.5rem;
}

#logo img {
  height: 100px;
  align-items: center;
  margin-left: 48%;
  border-radius: 40%;
  margin-top: 3px;
}

#video-container {
  position: fixed;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: black;
}

#video {
  width: 100%;
  height: 100%;
  object-fit: calc(100%);
}

.signin-btn:hover{
  color: #1b9241;
}
.signup-btn:hover{
  color: #1b9241;
  background-color: #222;
}
@media (prefers-reduced-motion: no-preference) {
  #logo img{
    animation: App-logo-spin infinite 10s linear;
    animation-iteration-count: 1;
    animation-duration: 4s;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
